<script>
import { Icon } from '@iconify/vue2';
export default {
  name: "icon",
  components: {
    Icon,
  },
  data() {
    return {
      text: `
  ### icon独立组件
  #### 遇到的问题
  - icon和UI框架耦合
  - UI框架的icon数量少
  - 使用iconfont，导入icon图片，位置有偏移且不方便
  #### 处理方案
  - 使用一个独立于UI框架的icon独立组件,**即插即用**
  #### iconify --- vue2环境下安装
  \`\`\`
  npm install --save-dev @iconify/vue2
  \`\`\`
  #### iconify --- vue3环境下安装
  \`\`\`
  npm install --save-dev @iconify/vue
  \`\`\`
  #### 引用
  \`\`\`
  // vue2
  import { Icon } from '@iconify/vue2';
  // vue3
  import { Icon } from '@iconify/vue';
  export default {
    components: {
      Icon,
    },
  });
  \`\`\`
  #### 基础使用
  \`\`\`
  // pug
  Icon(icon="svg-spinners:3-dots-rotate")
  \`\`\`
  这是svg-spinners:3-dots-rotate对应的icon，效果如下：
      `,
      text2: `
  #### 寻找svg/仓库地址
  https://icon-sets.iconify.design/
  > 在网站地址上可以设置icon**朝向，大小，颜色**
  #### 这是上面地址里，一些看着不错的示例库
      `,
    };
  },
}
</script>

